*{
margin: 0;
padding: 0;
}

*{box-sizing: border-box;}

#::after {box-sizing: border-box;}
#::before {box-sizing: border-box;}

#html {
word-break: break-all;
}

#div1 {
position: fixed;
right: 50%;
top: 10px;
}

#div1::before {
content: '';
display: block;
position: absolute;
}

#div1::after {
content: '';
display: block;
position: absolute;
}

@media(max-width: 500px) {
#html {
height: 50vh;
overflow: auto;
}

#divWrapper {
height: 50vh;
}

#div1 {
position: relative;
top: 0;
left: 25%;
}
}